.card{
    position: relative;
    width: 215px;
    background-color: whitesmoke;
    border: 3px solid lightgrey;
    border-right: 3px solid lightslategray;
    border-bottom: 3px solid lightslategray;
    border-radius: 20px;
    cursor: pointer;
}
.character-image{
    border-right: 3px solid lightslategray;
    width: 170px;
    height: 160px;
    border-radius: 20px;
    margin: 10px;
    margin-bottom: 35px;
}
.info{
    position: absolute;
    top: 177px;
    right: 1px;
    width: 200px;
}
.name{
    cursor: pointer;
    font-weight:bold;
    text-decoration-thickness: 2px;
}
.star{
    position: absolute;
    right: 10px;
    top: 8px;
    height: 30px;
    width: 33px;
    cursor: pointer;
    background-color: whitesmoke;
    border-radius: 30%;
    border: none;
    background-image: url('./star-regular.svg');
}
.star:hover{
    background-image: url('./star-regular-hover.svg');
}
.changed{
    background-image: url('./star-solid.svg') !important;
} 