.characters-image{
    width: 160px;
    height: 160px;
    border-radius: 20px;
    margin: 10px;
    border-right: 3px solid lightslategray;
}
.characters-info{
    position: absolute;
    width: 600px;
    height: 160px;
    background-color: whitesmoke;
    right: 260px;
    top:173px;
    border: 3px solid lightgrey;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}


